
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: auto;
  font-size: 30px;
}

.whale {
  margin-top: 200px;
}
.btn {
  width: 130px;
  height: 40px;
  margin: 10px 5px;
  display: inline-block;
  cursor: pointer;
  border-radius: 20px;
  color: #2c3e50;
  font-size: 13px;
  font-weight: bold;
  border-color: #2c3e50;
}
.btn:hover {
  background-color: #2c3e50;
  color: #ffffff;
}
